import styled from "styled-components";

const Container = styled.div`
  width: 400px;
  height: 290px;
  border: solid 1px black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;

  @media (max-width: 500px) {
    width: 80vw;
    height: auto;
  }

`;

const Img = styled.img`
  width: 100%;
  height: 185px;
  cursor: pointer;

  @media (max-width: 550px) {
    height: 34vw;
  }
`;

const ProjectComponent = (props) => {
    const { name, tags, fronturl, backurl, imgSrc, link } = props.project;
    return (
        <Container>
            <div>
                <Img src={imgSrc} onClick={()=>{window.location = link}}></Img>
                <h2>{name}</h2>
            </div>
            <div>
                <hr/>
                <p>{tags}</p>
                {backurl ?
                    <div>
                        <a href={fronturl}>Reposit贸rio Frontend</a>
                        <br />
                        <a href={backurl}>Reposit贸rio Backend</a>
                    </div> :
                    <div>
                        <a href={fronturl}>Reposit贸rio Frontend</a>
                    </div>}
            </div>
        </Container>
    );
};

export default ProjectComponent;
